import * as React from "react"
import { graphql, Link } from "gatsby"

import Seo from "../components/seo/Seo"
import Layout from "../components/layout/Layout"
import PostCard from "../components/blog/PostCard"
import { slugify } from "../utils/slugify"

const groupPostsByCategory = posts => {
  const byCategory = new Map()

  posts.forEach(post => {
    const name = post.frontmatter.category || "Uncategorized"
    if (!byCategory.has(name)) {
      byCategory.set(name, [])
    }
    byCategory.get(name).push(post)
  })

  return Array.from(byCategory.entries())
}

const BlogIndexPage = ({ data }) => {
  const posts = data.allMdx.nodes || []
  const popularPosts = data.popular.nodes || []
  const categories = groupPostsByCategory(posts)

  return (
    <Layout>
      <div className="max-w-[1200px] mx-auto px-4 py-10">
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-10">
          <div className="lg:col-span-3 space-y-10">
            {categories.map(([categoryName, categoryPosts]) => {
              const slug = `/blog/category/${slugify(categoryName)}/`
              const samplePosts = categoryPosts.slice(0, 3)

              return (
                <section key={categoryName}>
                  <header className="mb-4">
                    <p
                      className="text-[var(--color-editorial-grey)] mb-1"
                      style={{ fontSize: "var(--text-xs)", letterSpacing: "0.18em", textTransform: "uppercase" }}
                    >
                      Category
                    </p>
                    <Link
                      to={slug}
                      className="text-[var(--color-editorial-black)] hover:text-[var(--color-editorial-accent)] transition-colors"
                      style={{ fontSize: "var(--text-lg)", fontWeight: 600 }}
                    >
                      {categoryName}
                    </Link>
                  </header>

                  <div className="space-y-4">
                    {samplePosts.map(post => (
                      <Link key={post.fields.slug} to={post.fields.slug}>
                        <PostCard
                          variant="small"
                          image={post.frontmatter.cover}
                          category={post.frontmatter.category}
                          title={post.frontmatter.title}
                          author="Hosthustle"
                          date={post.frontmatter.date}
                        />
                      </Link>
                    ))}
                  </div>
                </section>
              )
            })}
          </div>

          <aside className="lg:border-l lg:border-[var(--color-editorial-border)] lg:pl-8">
            <div className="mb-6">
              <h2
                className="text-[var(--color-editorial-black)]"
                style={{ fontSize: "var(--text-base)", fontWeight: 600 }}
              >
                Popular Posts
              </h2>
            </div>

            <div className="space-y-6">
              {popularPosts.map((post, index) => (
                <div key={post.fields.slug} className="flex gap-4">
                  <div
                    className="text-[var(--color-editorial-grey)]"
                    style={{ fontSize: "var(--text-xs)", fontWeight: 600 }}
                  >
                    {(index + 1).toString().padStart(2, "0")}
                  </div>
                  <div className="space-y-1">
                    <Link
                      to={post.fields.slug}
                      className="text-[var(--color-editorial-black)] hover:text-[var(--color-editorial-accent)]"
                      style={{ fontSize: "var(--text-sm)", fontWeight: 600 }}
                    >
                      {post.frontmatter.title}
                    </Link>
                    <p
                      className="text-[var(--color-editorial-grey)]"
                      style={{ fontSize: "var(--text-xs)" }}
                    >
                      {post.frontmatter.date}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </aside>
        </div>
      </div>
    </Layout>
  )
}

export default BlogIndexPage

export const Head = () => <Seo title="Blogs" />

export const pageQuery = graphql`
  {
    allMdx(sort: { frontmatter: { date: DESC } }, limit: 1000) {
      nodes {
        excerpt(pruneLength: 200)
        fields {
          slug
        }
        frontmatter {
          title
          description
          category
          cover
          coverAlt
          date(formatString: "MMMM DD, YYYY")
        }
      }
    }
    popular: allMdx(sort: { frontmatter: { date: DESC } }, limit: 4) {
      nodes {
        fields {
          slug
        }
        frontmatter {
          title
          date(formatString: "MMMM DD, YYYY")
        }
      }
    }
  }
`
